import React from 'react';
import {GTSButton} from "./GTSButton";
import {NestedList} from "./NestedList";
export class Page2 extends React.Component{
	constructor(props) {
		super(props);
		this.timer_time=1000;
		this.state = {
			response_data:{},
			intervalId:null,
			lines_data:[],
			exit:false,
			show_button:false,
		};
	}
	set_response_data=(new_response_data)=>{
		this.setState({
			response_data:this.state.response_data=new_response_data
		});
	}
	set_lines_data=(new_lines_data)=>{
		this.setState({
			lines_data:this.state.lines_data=new_lines_data
		});
	}
	set_exit=(new_exit)=>{
		this.setState({
			exit:new_exit
		});
	}
	set_show_button=()=>{
		this.setState({
			show_button:true
		});
	}
	getreq=()=>{
		const axios = require('axios');
		axios({
			method: 'get',
			url: '/get_current_det',
		})
		.then(response =>{
			if(response.status===200){
				this.set_response_data(response.data)
			}
		})
		.catch(err => console.warn(err));
	}
	timer=()=>{
		let new_exit=false;
		this.getreq()
		let new_lines_data=[]
		for(let i in this.state.response_data){
			if(i!=="exit"){
				let new_lines_data_i={}
				let buf=this.state.response_data[i]
				new_lines_data_i["key"]=i
				new_lines_data_i["label"]="Line "+i+":"+buf[0]
				new_lines_data_i["items"]=[]
				buf=buf[1]
				for(let j in buf){
					let new_lines_data_i_j={}
					new_lines_data_i_j["key"]=j
					new_lines_data_i_j["label"]=buf[j][0]+":"+buf[j][1]
					new_lines_data_i["items"].push(new_lines_data_i_j)
				}
				new_lines_data.push(new_lines_data_i)
			}
			if(i==="exit"){
				this.set_exit(this.state.response_data[i])
			}
		}
		this.set_lines_data(new_lines_data)
		if(this.state.exit===true){
			clearInterval(this.state.intervalId);
			this.set_show_button()
		}
	}
	componentDidUpdate(prevProps){
		if((this.props.page!==prevProps.page)&&(this.props.page===2)){
			var intervalId = setInterval(this.timer, this.timer_time);
			this.setState({intervalId: intervalId});
		}
	}
	render(){
		if(this.props.page===2){
			let w=parseInt(this.props.width*0.5)+"px"
			let h=parseInt(this.props.height*0.5)+"px"
			return(
				<React.Fragment>
					<div
						style={{
							width:w,
							height:h,
							position:"relative",
							display:"flex",
							margin:"auto",
							backgroundColor:"black"
						}}
					>
						<img
							style={{
								width:w,
								height:h,
								zIndex:1,
								display:"flex",
								visibility:"visible",
							}}
							alt="stream"
							id="video"
							src={this.props.stream_url}
							onError={(e)=>
										{
											e.target.onerror = null;
											e.target.src=this.props.Background
										}
									}
						/>
					</div>
					<GTSButton
						show_button={this.state.show_button}
						set_page_to_3={this.props.set_page_to_3}
						set_sim={this.props.set_sim}
						set_rec_gates={this.props.set_rec_gates}
						set_empty_frame={this.props.set_empty_frame}
					/>
					<NestedList
						lists={this.state.lines_data}
					/>
				</React.Fragment>
			);
		}
		else{
			return(null);
		}
	}
}
