import React from 'react';
import Button from '@material-ui/core/Button';
export function GTSButton(props) {
	const go_to_stats_button="Show stats";
	const postreq=()=>{
		const axios = require('axios');
		axios({
			method: 'post',
			url: '/get_stats',
		})
		.then(response =>{
			if(response.status===200){
				props.set_sim()
				axios({
					method: 'post',
					url: '/get_rec_gates',
				})
				.then(response =>{
					if(response.status===200){
						props.set_rec_gates()
					}
				})
				.catch(err => console.warn(err));
			}
		})
		.catch(err => console.warn(err));
		/*axios({
			method: 'post',
			url: '/get_empty_frame',
		})
		.then(response =>{
			if(response.status===200){
				props.set_empty_frame()
			}
		})
		.catch(err => console.warn(err));*/
	}
	const handleClick = event => {
		postreq()
		props.set_page_to_3()
	}
	if(props.show_button===true){
		return(
			<Button
				variant="outlined"
				color="primary"
				onClick={handleClick}
				style={{
					width:"50%",
					position:"relative",
					display:"flex",
					margin:"auto",
					marginTop:"25px",
					marginBottom:"25px",
				}}
			>
				{go_to_stats_button}
			</Button>
		);
	}
	else {
		return(null);
	}
}
