import React from 'react';
import Gallery from 'react-grid-gallery';
import Typography from '@material-ui/core/Typography';
//import 'typeface-roboto';
import ImageZoom from 'react-medium-image-zoom'

const headers=[
	"Stats vizualization",
	"Clusterized clear ways",
	"Frame without detectable objets",
]

export function Page3(props) {
	if(props.page===3){
		let w=parseInt(props.width*0.2)+"px"
		let h=parseInt(props.height*0.2)+"px"
		let w_big=parseInt(props.width*0.5)+"px"
		let h_big=parseInt(props.height*0.5)+"px"
		const IMAGES =
		[
			{
				src: props.s_im_0,
				thumbnail: props.s_im_0,
				thumbnailWidth: w,
				thumbnailHeight: h,
				caption: "All detections"
			},
			{
					src: props.s_im_1,
					thumbnail: props.s_im_1,
					thumbnailWidth: w,
					thumbnailHeight: h,
					caption: "Trajectories"
			},
			{
					src: props.s_im_2,
					thumbnail: props.s_im_2,
					thumbnailWidth: w,
					thumbnailHeight: h,
					caption: "Smooth and clear trajectories"
			},
			{
					src: props.s_im_3,
					thumbnail: props.s_im_3,
					thumbnailWidth: w,
					thumbnailHeight: h,
					caption: "Time(sec) heetmap"
			},
			{
					src: props.s_im_4,
					thumbnail: props.s_im_4,
					thumbnailWidth: w,
					thumbnailHeight: h,
					caption: "Trajectory length(pixels) heetmap"
			}
		]
		return(
			<React.Fragment>
				<Typography
					variant="h2"
					gutterBottom
					align={"center"}
				>
					{headers[0]}
				</Typography>
				<div
					style={{
						display: "block",
						minHeight: "1px",
						width: "100%",
						overflow: "auto"
					}}
				>
					<Gallery
						enableImageSelection={false}
						images={IMAGES}
					/>
				</div>
				<Typography
					variant="h2"
					gutterBottom
					align={"center"}
					style={{
						display:"block",
						marginTop:"50px"
					}}
				>
					{headers[1]}
				</Typography>
				<ImageZoom
					image={{
						src: props.rec_gates,
						alt: headers[1],
						className: 'img',
						style: {
							display:"flex",
							margin:"auto",
							width:w_big,
							height:h_big,
						 }
					}}
					zoomImage={{
						src: props.rec_gates,
						alt: headers[1]
					}}
				/>
			</React.Fragment>
		);
	}
	else{
		return(null);
	}
}


