import React from 'react';
export class MyCanvas extends React.Component{
	constructor(props) {
		super(props);
		this.W=this.props.W;
		this.H=this.props.H;
		this.k = 0;
		this.line_width=1;
		this.canvasRef = React.createRef();
		this.handleClick = this.handleClick.bind(this);
	}
	handleClick(e) {
		e.preventDefault();
		let canvas = this.canvasRef.current;
		let ctx = canvas.getContext('2d');
		let rect = canvas.getBoundingClientRect();
		let x = e.clientX- rect.left;
		let y = e.clientY- rect.top;
		if(this.k===0){
			this.props.setPoint(x,y)
		}
		else{
			let xperp0=Math.round((x+this.props.point[0])/2);
			let yperp0=Math.round((y+this.props.point[1])/2);
			let xV=x-this.props.point[0];
			let yV=y-this.props.point[1];
			let yVperp=1;
			let xVperp=1;
			if(yV!==0){
				yVperp=-(xV/yV)
			}
			else{
				xVperp=0;
			}
			if(x>this.props.point[0]){
				if(yVperp<0){
					yVperp=-yVperp;
					xVperp=-xVperp;
				}
			}
			if(x<this.props.point[0]){
				if(yVperp>0){
					yVperp=-yVperp;
					xVperp=-xVperp;
				}
			}
			if(x===this.props.point[0]){
				if(xVperp<0){
					yVperp=-yVperp;
					xVperp=-xVperp;
				}
			}
			let l=Math.sqrt(xVperp*xVperp+yVperp*yVperp)
			let etaL=50;
			xVperp=Math.round(xVperp*etaL/l)
			yVperp=Math.round(yVperp*etaL/l)
			let xperp1=Math.round(xVperp+xperp0)
			let yperp1=Math.round(yVperp+yperp0)
			this.props.setLine(x,y,xperp0,yperp0,xperp1,yperp1)
		}
		this.k++;
		this.k=this.k%2;
	}
	drawline(ctx,line){
		ctx.beginPath()
		ctx.arc(line['x0'], line['y0'], this.line_width, 0, Math.PI * 2, true);
		ctx.arc(line['x1'], line['y1'], this.line_width, 0, Math.PI * 2, true);
		ctx.fill();
		ctx.beginPath();
		ctx.lineWidth = 2*this.line_width;
		ctx.moveTo(line['x0'], line['y0']);
		ctx.lineTo(line['x1'], line['y1']);
		ctx.stroke();
	}
	drawarrow(ctx,line){
		ctx.beginPath()
		ctx.arc(line['x0'], line['y0'], this.line_width, 0, Math.PI * 2, true);
		ctx.arc(line['x1'], line['y1'], this.line_width, 0, Math.PI * 2, true);
		ctx.fill();
		ctx.beginPath();
		ctx.lineWidth = 2*this.line_width;
		ctx.moveTo(line['x0'], line['y0']);
		ctx.lineTo(line['x1'], line['y1']);
		ctx.stroke();
		let radius=10;
		let angle = Math.atan2(line['y1'] - line['y0'], line['x1'] - line['x0']);
		let x = radius * Math.cos(angle) + line['x1'];
		let y = radius * Math.sin(angle) + line['y1'];
		ctx.moveTo(x, y);
		angle += (1.0/3.0) * (2 * Math.PI);
		x = radius * Math.cos(angle) + line['x1'];
		y = radius * Math.sin(angle) + line['y1'];
		ctx.lineTo(x, y);
		angle += (1.0/3.0) * (2 * Math.PI);
		x = radius *Math.cos(angle) + line['x1'];
		y = radius *Math.sin(angle) + line['y1'];
		ctx.lineTo(x, y);
		ctx.closePath();
		ctx.fill();
	}
	drawpoint(ctx){
		ctx.beginPath()
		ctx.arc(this.props.point[0], this.props.point[1], this.line_width, 0, Math.PI * 2, true);
		ctx.fill();
	}
	draw(){
		if(this.canvasRef.current!==null){
			let canvas = this.canvasRef.current
			let ctx = canvas.getContext('2d')
			ctx.clearRect(0, 0, canvas.width, canvas.height);
			let color=this.props.color_lines;
			ctx.fillStyle = color;
			ctx.strokeStyle = color;
			for(let i=0;i<this.props.lines.length;i++){
				this.drawline(ctx,this.props.lines[i])
				this.drawarrow(ctx,this.props.perp[i])
			}
			if(this.props.point!==null){
				this.drawpoint(ctx);
			}
			color=this.props.color_lines_marked;
			ctx.fillStyle = color;
			ctx.strokeStyle = color;
			for(let i=0;i<this.props.marked.length;i++){
				this.drawline(ctx,this.props.lines[this.props.marked[i]])
				this.drawarrow(ctx,this.props.perp[this.props.marked[i]])
			}
		}
	}
	render(){
		this.draw();
		return(
			<div
				width={this.W}
				height={this.H}
			>
				<img
					alt={"First frame"}
					src={this.props.im_src}
					width={this.W}
					height={this.H}
					style={{
						zIndex:1,
						display:"flex",
						visibility:"visible",
						left: 0,
						right: 0,
						margin:"auto",
						position:"absolute",
						align:"center"
					}}
				/>
				<canvas
					ref={this.canvasRef}
					style={{
						position:"relative",
						display:"flex",
						margin:"auto",
						zIndex:20
					}}
					width={this.W}
					height={this.H}
					onClick={this.handleClick}
				>
				</canvas>
			</div>
		);
	}
}
