import React from 'react';
import ReactDOM from 'react-dom';
import {MuiThemeProvider, createMuiTheme} from '@material-ui/core/styles';
import red from '@material-ui/core/colors/red';
import green from '@material-ui/core/colors/green';
import {Page0} from "./components/Page0/Page0";
import {Page1} from "./components/Page1/Page1";
import {Page2} from "./components/Page2/Page2";
import {Page3} from "./components/Page3/Page3";
import Background from './images/default.png';
const color1=green;
const color2=red;

const color_lines=color1['500'];
const color_lines_marked=color2['500'];

const theme = createMuiTheme({
	spacing: 4,
	palette: {
		type:'light',
		primary: color1,
		secondary: color2,
	},
	status: {
		danger: 'orange',
	},
});


class App extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			width:0,
			height:0,
			page:0,
			im_src:Background,
			stream_url:"",
			num_of_lines:0,
			button_to_stats:true,
			s_im_0:Background,
			s_im_1:Background,
			s_im_2:Background,
			s_im_3:Background,
			s_im_4:Background,
			empty_frame:Background,
			rec_gates:Background
		};
	}
	set_button_to_stats=()=>{
		this.setState({
			button_to_stats:true
		});
	}
	set_page_to_1=()=>{
		this.setState({
			page:1
		});
	}
	set_page_to_2=()=>{
		this.setState({
			page:2
		});
	}
	set_page_to_3=()=>{
		this.setState({
			page:3
		});
	}
	set_im_src=()=>{
		this.setState({
			im_src:"http://127.0.0.1:5000/get_image"
		});
	}
	set_streamurl=()=>{
		this.setState({
			stream_url:"http://127.0.0.1:5000/get_stream"
		});
	}
	set_sim=()=>{
		this.setState({
			s_im_0:"http://127.0.0.1:5000/get_stats0",
			s_im_1:"http://127.0.0.1:5000/get_stats1",
			s_im_2:"http://127.0.0.1:5000/get_stats2",
			s_im_3:"http://127.0.0.1:5000/get_stats3",
			s_im_4:"http://127.0.0.1:5000/get_stats4",
		});
	}
	set_empty_frame=()=>{
		this.setState({
			empty_frame:"http://127.0.0.1:5000/get_empty",
		})
	}
	set_rec_gates=()=>{
		this.setState({
			rec_gates:"http://127.0.0.1:5000/get_rec",
		})
	}
	set_num_of_lines=(n)=>{
		this.setState({
			num_of_lines:n
		});
	}
	componentDidMount() {
		this.updateWindowDimensions();
		window.addEventListener('resize', this.updateWindowDimensions);
	}
	componentWillUnmount() {
		window.removeEventListener('resize', this.updateWindowDimensions);
	}
	updateWindowDimensions=()=>{
		this.setState({ width: window.innerWidth, height: window.innerHeight });
	}
	render(){
		console.log(color1['500'])
		return (
			<MuiThemeProvider theme={theme}>
				<div>
					<Page0
						page={this.state.page}
						set_page_to_1={this.set_page_to_1}
						set_im_src={this.set_im_src}
						width={this.state.width}
						height={this.state.height}
					/>
					<Page1
						page={this.state.page}
						set_page_to_2={this.set_page_to_2}
						im_src={this.state.im_src}
						color_lines={color1['500']}
						color_lines_marked={color2['500']}
						set_streamurl={this.set_streamurl}
						width={this.state.width}
						height={this.state.height}
						set_num_of_lines={this.set_num_of_lines}
					/>
					<Page2
						Background={Background}
						page={this.state.page}
						set_page_to_3={this.set_page_to_3}
						stream_url={this.state.stream_url}
						set_sim={this.set_sim}
						set_rec_gates={this.set_rec_gates}
						set_empty_frame={this.set_empty_frame}
						width={this.state.width}
						height={this.state.height}
						num_of_lines={this.state.num_of_lines}
					/>
					<Page3
						page={this.state.page}
						s_im_0={this.state.s_im_0}
						s_im_1={this.state.s_im_1}
						s_im_2={this.state.s_im_2}
						s_im_3={this.state.s_im_3}
						s_im_4={this.state.s_im_4}
						rec_gates={this.state.rec_gates}
						empty_frame={this.state.empty_frame}
						width={this.state.width}
						height={this.state.height}
					/>
				</div>
			</MuiThemeProvider>
		);
	}
}

ReactDOM.render(<App />, document.getElementById('root'));
