import React from 'react';
import Button from '@material-ui/core/Button';
import {EnhancedTable} from './EnhancedTable'
import {MyCanvas} from './MyCanvas'

function createData(name,x0, y0, x1, y1) {
	return {name, x0, y0, x1, y1 };
}

export class Page1 extends React.Component{
	constructor(props) {
		super(props);
		this.run_button="Run"
		this.W=800;
		this.H=450;
		this.state = {
			point:null,
			line_num:0,
			lines:[],
			perp:[],
			marked:[],
			stage:0,
		};
	}
	setPoint=(x,y)=>{
		this.setState({
			point:[x,y]
		});
	}
	setLine=(x,y,xperp0,yperp0,xperp1,yperp1)=>{
		this.setState({
			lines:this.state.lines.concat(
				createData(
					this.state.line_num,
					Math.ceil(this.state.point[0]),
					Math.ceil(this.state.point[1]),
					Math.ceil(x),
					Math.ceil(y)
				)),
			perp:this.state.perp.concat(
				createData(
					this.state.line_num,
					Math.ceil(xperp0),
					Math.ceil(yperp0),
					Math.ceil(xperp1),
					Math.ceil(yperp1)
				)),
			line_num: this.state.line_num+1,
			point:null
		});
	}
	load_marked=(marked)=>{
		this.setState({
				marked:this.state.marked=marked.slice()
			});
	}
	del_marked=(marked)=>{
		this.setState({
				marked:this.state.marked=[],
			});
		let newlines=this.state.lines.slice();
		let newperp=this.state.perp.slice();
		for(let i=0;i<marked.length;i++){
			newlines.splice(marked[i],1);
			newperp.splice(marked[i],1);
			for(let j=i+1;j<marked.length;j++)
				if(marked[j]>marked[i])marked[j]--;
		}
		for(let i=0;i<newlines.length;i++){
			newlines[i].name=i;
			newperp[i].name=i;
		}
		this.setState({
				lines:this.state.lines=newlines.slice(),
				perp:this.state.perp=newperp.slice(),
				line_num:newlines.length
			});
	}
	postreq=()=>{
		const axios = require('axios');
		axios({
			method: 'post',
			url: '',
			data: {
				lines: JSON.stringify(this.state.lines),
			}
		})
		.then(response =>{
			console.log(response)
		})
		.catch(err => console.warn(err));
	}
	handleClick=(e)=>{
		e.preventDefault();
		this.postreq()
		this.props.set_num_of_lines(this.state.line_num)
		this.props.set_streamurl();
		this.props.set_page_to_2();
	}
	render(){
		if(this.props.page===1){
			return (
				<div>
					<MyCanvas
						color_lines={this.props.color_lines}
						color_lines_marked={this.props.color_lines_marked}
						setPoint={this.setPoint}
						setLine={this.setLine}
						point={this.state.point}
						lines={this.state.lines}
						marked={this.state.marked}
						im_src={this.props.im_src}
						perp={this.state.perp}
						W={this.W}
						H={this.H}
					/>
					<Button
						variant="outlined"
						color="primary"
						onClick={this.handleClick}
						style={{
							width:"50%",
							position:"relative",
							display:"flex",
							margin:"auto",
							marginTop:"25px",
							marginBottom:"25px",
						}}
					>
						{this.run_button}
					</Button>
					<EnhancedTable
						load_marked={this.load_marked}
						rows={this.state.lines}
						del_marked={this.del_marked}
					/>
				</div>
			);
		}
		else{
			return(null);
		}
	}
}
