import React from "react";
import PropTypes from "prop-types";
/*import { withStyles } from "@material-ui/core/styles";
import ListSubheader from "@material-ui/core/ListSubheader";
import ListItemIcon from "@material-ui/core/ListItemIcon";*/
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";


const colors=[
	'#0000FF',
	'#00FF00',
	'#FF0000',
	'#00FFFF',
	'#FF00FF',
	'#FFFF00',
	'#000000',
	'#FFFFFF',
	'#8080FF',
	'#FF8080',
	'#80FF80',
	'#808080'
]

export class NestedList extends React.Component {
	state = { open: {} };

	handleClick = key => () => {
		console.log(key);
		this.setState({ [key]: !this.state[key] });
	};

	render() {
		const { lists, classes } = this.props;
		return (
			<div>
				<List
					component="nav"
				>
				{lists.map(({ key, label, items }) => {
					const open = this.state[key] || false;
					return (
						<div key={key}>
							<ListItem button onClick={this.handleClick(key)}>
							<ListItemText style={{ color:colors[key%12]}} inset primary={label} />
								{open ? <ExpandLess /> : <ExpandMore />}
							</ListItem>
							<Collapse in={open} timeout="auto" unmountOnExit>
								<List component="div" disablePadding>
									{items.map(({ key: childKey, label: childLabel}) => (
										<ListItem key={childKey} button>
											<ListItemText inset primary={childLabel} />
										</ListItem>
									))}
								</List>
							</Collapse>
						</div>
					);
				})}
				</List>
			</div>
		);
	}
}

NestedList.propTypes = {
  classes: PropTypes.object.isRequired
};
